<script>
// import { defineComponent, ref } from "vue";
import VueMeetingSelector from "vue-meeting-selector";
// import slotsGenerator from "vue-meeting-selector/src/helpers/slotsGenerator";

export default {
  data: function () {
    return {
      dates: [],
      parses: [],
      days: [],
      uniqday: [],
      c: ["2023-09-13", "2023-09-13"],
      final: [],
    };
  },
  components: { VueMeetingSelector },

  props: ["ave"],
  methods: {
    vies() {
      this.dates = this.ave.availability;
      console.log("dates", this.dates);
      for (var i = 0; i < this.dates.length; i++) {
        this.dates[i].date = this.ge(this.dates[i].date);
        this.days[i] = this.dates[i].date;
      }
      console.log("dayes", this.days);
    },

    ge(d) {
      var date = new Date(d);
      var dateString = date.toLocaleDateString("ar-EG", {
        weekday: "long",
        year: "numeric",
        month: "short",
        day: "numeric",
      });
      return dateString;
    },
    tt() {
      this.uniqday = [...new Set(this.days)];

      console.log("uc", this.uniqday);
    },
    times() {
      for (var i = 0; i < this.uniqday.length; i++) {
        for (var k = 0; i < this.dates.date.length; k++) {
          if (this.dates[k].date == this.uniqday[i]) {
            console.log("k", this.dates[k].start_time);
            this.final.push({
              date: this.dates[k].date,
              time: this.dates[k].start_time,
            });
          }
        }
      }
      console.log("final", this.final);
    },
  },
  created() {
    this.vies();
    // this.times();
    // this.ge();
    this.tt();
    this.times();
  },
};
</script>
